import React from "react";
import ViewButtons from "./ViewButtons";
import Sort from "../Sort";
import { useAppSelector } from "../../hooks/useAppSelector";
import { selectListView } from "../../store/products/selectors";

import "./style.scss";

const ViewOptions: React.FC = () => {
    const listView = useAppSelector(selectListView);

    return (
        <div className="view-options">
            {listView === "cards" && (
                <Sort>
                    <option value="empty"></option>
                    <option value="price_total-asc">  </option>
                    <option value="price_total-desc">  </option>
                    <option value="square-asc">  </option>
                    <option value="square-desc">  </option>
                    <option value="rooms-asc"> -  </option>
                    <option value="rooms-desc"> -  </option>
                    <option value="complex_name-asc">  </option>
                    <option value="complex_name-desc">  </option>
                    <option value="price_sq_m-asc"> /2 </option>
                    <option value="price_sq_m-desc"> /2 </option>
                </Sort>
            )}
            <ViewButtons />
        </div>
    );
};

export default ViewOptions;
